// Triple bars above needed for Jekyll processing

// $sans-font-stack: Verdana, Arial, Helvetica, sans-serif;
$sans-font-stack: Gill Sans, Gill Sans MT, Calibri, sans-serif;

$full-screen-sidenav-allotment: 200px;
$mobile-screen-sidenav-allotment: 40px;
$header-height: 50px;
$thin-space: 5px;
$med-space: 10px;

$cornflower: #4459e2;
$pale-cornflower: #8bb1ef;
$deep-cornflower: #224d93;
$light-gray: #ccc;
$smallscreen: 768px;

* { 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; 
}

html { background: white; }

body { 
  font-family: $sans-font-stack; 
  // Body has a default margin. Override.
  font-size: 120%;
  margin: 0px; 
}

// The left-hand bar with navigation links
.sidenav {
  height: 100%;
  width: $full-screen-sidenav-allotment;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  display: block;
  background-color: $light-gray;
  box-sizing: border-box; 
  transition: 0.3s; 
}

// An entry in the navigation menu
.entry {
 a {
    padding: 4px 8px 4px 20px;
    font-size: 120%;
    text-decoration: none;
    display: block;
    color: $cornflower;
  }

  a:hover {
      background-color: $pale-cornflower;
  }
}

.sidenav-header{
  height: $header-height + $thin-space;
  padding-left: $med-space;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;

  a {
    padding: 0;
    color: black;
    text-decoration: none;
  }

  a:visited {
    color: black;  
  }  

}

.page-header{
  height: $header-height;
  background-color: $pale-cornflower;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;
  padding-left: $med-space;
  position: relative;

  a {
    padding: 0;
    color: black;
    text-decoration: none;
  }

  a:visited {
    color: black;  
  }  
}

.burger-outer{
  float: left;
  width: 30px;
  height: 100%;
  position: relative;
}

// Button containing the burger SVG
.burger-button {
  background-color: $light-gray;
  width: 30px;
  height: 30px;

  // Center vertically in .burger-outer
  position: absolute;
  top: 50%;
  transform: translate(0px,-50%);

  border-radius: 6px;
  border-style: solid;
  border-width: 2px;
}

// Centers burger svg in the middle of its containing button
#burger-svg {
  display: block;
  margin: auto;
  height: 100%;
}


.main-body {
   // Clicks via 'onclick' make the screen flash on Safari mobile without this.
  -webkit-tap-highlight-color: transparent;
}


.page-header-title{
  padding-left: 40px;;
  width: 100%;
}

.page-content{
  padding-left: $med-space;

  a {
    color: $cornflower;
    text-decoration: none;
  }

  a:visited {
    color: $deep-cornflower;  
  }  
}

@media (max-width: $smallscreen - 1px){
  .sidenav{ width: 0; }
  .sidenav.showing{ width: $full-screen-sidenav-allotment }
  .hide-small{display:none!important}
  .main-body {
    margin-left: 0px;//$mobile-screen-sidenav-allotment;
  }
  .page-header{
    padding-right: 5px
  }
  .page-content{
    padding-right: 5px
  }
}

@media (min-width:$smallscreen){
  .hide-large{display:none!important}
  .sidenav{ width: $full-screen-sidenav-allotment; }
  .main-body {
    margin-left: $full-screen-sidenav-allotment; 
  }
  .page-header{
    padding-right: 20px
  }
  .page-content{
    padding-right: 20px
  }
}

pre code {
  background-color: #eee;
  border: 2px solid #999;
  display: block;
  padding: 20px;
  overflow-x: scroll;
}

.table-box{
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;  
  margin-right: 5px;
  margin-bottom: 5px;
}

table.asst-table {
  border: 2px solid black;
  border-collapse: separate;
  border-spacing: 20px 10px;

  td {
    white-space:nowrap;
    word-break:break-all;
    line-height: 130%;    
  }
}

dt {
  float: left;
  width: 30px;
  text-align: right;
  font-weight: bold;
}

dd {
  margin: 0 0 0 60px;
  padding: 0 0 0.5em 0;
}

div.x-scroll{
  overflow-x: scroll;  
  -webkit-overflow-scrolling: touch;  
  width: 100%;
  white-space: nowrap;
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  padding: 4px 8px 4px 20px;
  font-size: 120%;
  text-decoration: none;
  display: block;
  color: $cornflower;
  cursor: pointer;
  transition: 0.4s;
}

.accordion:hover {
    background-color: $pale-cornflower;
}

/* Style the accordion panel. Note: hidden by default */
.accordion-panel {
  padding: 0 18px;
  background-color: $light-gray;
  display: none;
  overflow: hidden;
}